.wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  line-height: 30px;
  color: $black;
}
.header__mobile {
  display: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
}

@media (max-width: 640px) {
  .header__mobile {
    width: 100%;
    display: block;
  }
}

li a:hover {
  transition-duration: 0.3s;
  color: rgb(200, 202, 241);
}
a:hover {
  transition-duration: 0.3s;
  color: rgb(200, 202, 241);
}
a img:hover {
  transition-duration: 0.3s;
  transform: scale(1.3);
}

li a:focus {
  transition-duration: 0.3s;
  color: rgb(200, 202, 241);
}
a:focus {
  transition-duration: 0.3s;
  color: rgb(200, 202, 241);
}
a img:focus {
  transition-duration: 0.3s;
  transform: scale(1.3);
}
