.bottom_section {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  margin: 8% auto;

  &__title {
    font-size: 20px;
    color: $cyan;
  }
  &__avatar-img {
    width: 28vw;

    margin-right: 7vh;
  }

  &__list {
    display: inline;
    clear: both;
  }

  & h2 {
    color: #171614;
    margin: 2vh 0;
    font-weight: bold;
    font-size: 2.8em;
    line-height: 54px;
  }

  &__list-item {
    padding-left: 40px;
    margin: 2vh 0;
    text-align: left;
    background: url(../img/checkmark.png) no-repeat;
  }

  &__text {
    margin: 5vh 0;
  }

  &__button {
    margin-top: 40px;
    margin: 24px 0;
    color: $white;
    background-color: $cyan;
    white-space: nowrap;
    padding: 2vh 2vw;
    border-radius: 25px;
  }

  //mobile version
  @media (max-width: 640px) {
    & {
      margin: 3vh auto;
      flex-direction: column-reverse;
      width: 90%;

      &__avatar-img {
        width: 100%;
        margin: 0;
        height: auto;
        margin-top: 5vh;
      }
    }
  }
  //tablet version
  @media (max-width: 1280px) {
    & {
      width: 90%;
      display: block;
      margin: 50px;
    }
  }
}
